<template>
<div id="user_info">
	 <div class="tit">个人信息</div>
	 <div class="warn">
	 	<img src="/static/icon/5.png">
			注意事项：如需更改绑定手机号,请联系客服。
	 </div>
	<div class="user_info">
		<div class="imgs">K</div>
		<div class="info_right">
			<div class="acc_num">
			   商户编号:
			<span>{{user.Id}}</span>&nbsp;
			</div>
			<div class="associated">
			  <div class="associat"><img src="/static/icon/6.png" width="13">
				<div>手机号码：{{user.phone}}</div>
			  </div>
			</div>
		</div>
	</div>
	<div class="user_infos">
	    <div class="user_contact">
			<div>Token</div>
			<div class="con_right">
				<input type="text" value=""  v-model="user.token" class="input_info" disabled="">
			</div>
		</div>
		<div class="user_contact">
			<div>商户类型</div>
			<div class="con_right">
				<input type="text" value="普通商户"  class="input_info" disabled="">
			</div>
		</div>
		<div class="user_contact">
			<div>单笔订单费率</div>
			<div class="con_right">
				<input type="text" value=""  v-model="user.order_service"
         class="input_info" disabled="">
			</div>
		</div>
		<div class="user_contact">
			<div>谷歌验证</div>
			<div class="con_right">
			  <div class="sel_box" :class="user.safety == 1 ? '' : 'sel_after'" @click.stop="ajaxclose">关闭</div>
			  <div class="sel_box" :class="user.safety == 1 ? 'sel_after' : ''" @click.stop="ajaxopen">开启</div>
			</div>
		</div>
		<div class="user_contact" v-if="google">
			<div>请绑定谷歌验证</div>
			<div class="con_right">
			<p>请在谷歌身份验证器输入秘钥<span style="font-weight: 700;font-size: 15px;color: #386cfa;">{{safety_code}}</span>,或扫以下二维码添加即可</p>
			<img :src="safety_url">
			</div>
		</div>
		<div class="user_contact" v-if="code">
			<div>请输入谷歌验证码</div>
			<div class="con_right">
				<input type="number" v-model="google_code" class="input_info">
			</div>
		</div>
		<Button v-if="code" type="primary" :loading="loading" @click="ajaxform" style="margin-top: 40px;margin-left: 20px;width: 98px;">
        <span v-if="!loading">保存</span>
        <span v-else>正在提交</span>
    </Button>
	</div>
</div>
</template>
<script>
export default {
     data () {
        return {
          spin:true,
          user:"",
          loading:false,
          google:false,
          code:false,
          google_code:'',
          safety_code:"",
          safety_url:""
        }
    },
    mounted(){
		   this.ajaxinfo();
		},
	methods: {
	    ajaxinfo(){
          this.user = JSON.parse(localStorage.getItem('user_info')).data;
          this.user.order_service = this.user.order_service + "%";
	    },
	    ajaxopen(){
        this.$http.get('api/user/google_change',{code:100}, res => {
             if (res.status == 200) {
                 if(res.data.code == 200){
                    this.safety_code = res.data.data.safety_code;
                    this.safety_url  = "http://qr.topscan.com/api.php?text=" + res.data.data.safety_url;
                    this.google = true;
                    this.user.safety = 1;
                    var userinfo = {};
                        userinfo.time = new Date().getTime() + 10000;
                        userinfo.data = this.user;
                    localStorage.setItem('user_info',JSON.stringify(userinfo));
                    this.$Message.success(res.data.msg);
                 }else if(res.data.code == 201){
                    this.$Message.warning(res.data.msg);
                 }
             }
        });
	    },
	    ajaxclose(){
        this.code = true;
	    },
	    ajaxform(){
        if(this.google_code == ""){
          this.$Message.warning("请输入谷歌验证码");
        }else{
          this.loading = true;
          this.$http.get('api/user/google_change',{code:101,google_code:this.google_code}, res => {
                  this.loading = false;
               if (res.status == 200) {
                   if(res.data.code == 200){
                      this.google = false;
                      this.code = false;
                      this.user.safety = 2;
                      localStorage.setItem('user_info',JSON.stringify(userinfo));
                      this.$Message.success(res.data.msg);
                   }else if(res.data.code == 201){
                      this.$Message.warning(res.data.msg);
                   }
               }
          });
        }
	    }
	}
}
</script>



